.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  
}

.App h1 {
  text-align: center;
  font-family: "Lucida Console", Monaco, monospace;
  font-size: 65px;
  letter-spacing: 1px;
  word-spacing: -0.4px;
  color: #f9fafc;
  font-weight: 700;
  text-decoration: underline solid rgb(8, 107, 236);
  font-style: normal;
  font-variant: small-caps;
  text-transform: capitalize;
  
}





